import { LockClosedIcon } from '@heroicons/react/outline'
import React, { useState } from 'react'
import { ToastContainer, toast } from 'react-toastify'
import { useDispatch } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import axios from 'axios'
import { setDummyUser } from '../../store/auth'
import 'react-toastify/dist/ReactToastify.css'

function Login() {
  // const user = useSelector((state) => state.auth.user)
  const dummy = true
  const [form, setForm] = useState({
    email: 'user',
    password: 'user',
  })
  const dispatch = useDispatch()
  const routerNavigate = useNavigate()

  const login = {
    email: 'user',
    password: 'user',
  }

  function handleChange(e) {
    const { name, value } = e.target
    setForm((values) => ({ ...values, [name]: value }))
  }

  function handleSubmit(e) {
    e.preventDefault()
    if (!dummy) {
      axios.get('http://localhost:8000/sanctum/csrf-cookie').then(() => {
        axios.post('http://localhost:8000/login', {
          username: form.email,
          password: form.password,
        }).then((res) => {
          if (res.data.status) {
            window.location = ''
          } else {
            toast.error(res.data.message, {
              hideProgressBar: true,
            })
          }
        }).catch(() => {
          toast.error('An error occured', {
            hideProgressBar: true,
          })
        })
      })
        .catch(() => {
          toast.error('An error occured', {
            hideProgressBar: true,
          })
        })
    } else {
      // eslint-disable-next-line no-lonely-if
      if (form.email === login.email && form.password === login.password) {
        dispatch(setDummyUser())
        routerNavigate('/')
      } else {
        toast.error('Incorrect username or password', {
          hideProgressBar: true,
        })
      }
    }
  }
  return (
    <div className="min-h-screen dark:bg-gray-900">
      <div
        className="
          min-h-full
          flex
          items-center
          justify-center
          py-12
          px-4
          sm:px-6
          lg:px-8
        "
      >
        <div className="max-w-md w-full space-y-8">
          <div>
            <img
              className="mx-auto h-12 w-auto"
              src="~/assets/img/icon.png"
              alt="ramp"
            />
            <h2 className="mt-6 text-center text-3xl font-extrabold text-gray-900 dark:text-gray-300">
              Login
            </h2>
          </div>
          <form
            className="mt-8 space-y-6"
            onSubmit={handleSubmit}
          >
            <div className="rounded-md shadow-sm -space-y-px">
              <div>
                <label htmlFor="email-address" className="sr-only">Email/username</label>
                <input
                  id="email-address"
                  name="email"
                  value={form.email || ''}
                  onChange={handleChange}
                  type="text"
                  autoComplete="email"
                  required
                  className="
                    appearance-none
                    rounded-none
                    relative
                    block
                    w-full
                    px-3
                    py-2
                    border border-gray-300
                    placeholder-gray-500
                    text-gray-900
                    rounded-t-md
                    focus:outline-none
                    focus:ring-indigo-500
                    focus:border-indigo-500
                    focus:z-10
                    sm:text-sm
                    dark:bg-gray-800
                    dark:text-gray-300
                    dark:border-gray-700
                  "
                  placeholder="Email/username"
                />
              </div>
              <div className="relative">
                <label htmlFor="password" className="sr-only">Password</label>
                <input
                  id="password"
                  name="password"
                  type="password"
                  value={form.password || ''}
                  onChange={handleChange}
                  autoComplete="current-password"
                  required
                  className="
                    appearance-none
                    rounded-none
                    relative
                    block
                    w-full
                    px-3
                    py-2
                    border border-gray-300
                    placeholder-gray-500
                    text-gray-900
                    rounded-b-md
                    focus:outline-none
                    focus:ring-indigo-500
                    focus:border-indigo-500
                    focus:z-10
                    sm:text-sm
                    dark:bg-gray-800
                    dark:text-gray-300
                    dark:border-gray-700
                  "
                  placeholder="Password"
                />
              </div>
            </div>
            <div>
              <button
                type="submit"
                className="
                  group
                  relative
                  w-full
                  flex
                  justify-center
                  py-2
                  px-4
                  border border-transparent
                  text-sm
                  font-medium
                  rounded-md
                  text-white
                  focus:outline-none
                  focus:ring-2
                  focus:ring-offset-2
                  focus:ring-indigo-500
                  bg-indigo-600 hover:bg-indigo-700
                "
              >
                <span className="absolute left-0 inset-y-0 flex items-center pl-3">
                  <LockClosedIcon
                    className="text-indigo-500 group-hover:text-indigo-400 w-5 h-5"
                  />
                </span>
                Login
              </button>
            </div>
          </form>
        </div>
      </div>
      <ToastContainer />
    </div>
  )
}

export default Login
